<template>
<div>
    <el-button :disabled="aSelectRow.length<1" @click="handlePassMulti('1')" v-if="isRole()">审核通过</el-button>
    <el-button :disabled="aSelectRow.length<1" @click="handlePassMulti('2')" v-if="isRole()">作废</el-button>
    <el-table ref="multipleTable" :data="aTableDataEmail" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectRows">
        <el-table-column type="selection" width="50"></el-table-column>
        <el-table-column prop="id" label="ID" min-width="70"></el-table-column>
        <el-table-column prop="time" label="申请时间" min-width="100" show-overflow-tooltip></el-table-column>
        <el-table-column prop="name" label="申请人" min-width="100" show-overflow-tooltip></el-table-column>
        <el-table-column prop="reason" label="申请原因" min-width="100" show-overflow-tooltip></el-table-column>
        <el-table-column prop="status" label="审批结果" min-width="100" show-overflow-tooltip>
            <template slot-scope="scope">
                <span>{{scope.row.status | statusFilter}}</span>
            </template>
        </el-table-column>
        <el-table-column prop="approverName" label="审批人" min-width="100" show-overflow-tooltip></el-table-column>
        <el-table-column label="操作" min-width="100">
            <template slot-scope="scope">
                <el-button @click.native.prevent="handleClickDetail(scope.row)" type="text" size="small">详情</el-button>
            </template>
        </el-table-column>
    </el-table>
    <!-- 分页 -->
    <el-pagination class="pagination" background layout="prev, pager, next" @current-change="handlePageChange" :current-page.sync="objPager.currentPage" :page-size.sync="objPager.pageSize" :total="objPager.total"></el-pagination>
    <!-- 邮件详情模态框 -->
    <el-dialog title="邮件详情" :visible.sync="bDialogVisible">
        <el-form>
            <el-form-item label="审批结果">
                <p>{{objFormEmailSingle.status | statusFilter}}</p>
            </el-form-item>
            <el-form-item label="服务器">
                <p>{{returnServer(objFormEmailSingle.serverId)}}</p>
            </el-form-item>
            <el-form-item label="收件人">
                <p>{{objFormEmailSingle.receiverType}}</p>
            </el-form-item>
            <el-form-item label prop="params" v-if="objFormEmailSingle.receiverType&&objFormEmailSingle.receiverType!=='1'">
                <p>{{objFormEmailSingle.params}}</p>
            </el-form-item>

            <el-form-item label="标题" prop="title">
                <p>{{objFormEmailSingle.title}}</p>
            </el-form-item>
            <el-form-item label="内容" prop="content">
                <p>{{objFormEmailSingle.content}}</p>
            </el-form-item>
            <el-form-item label="开始发奖时间" prop="startRewardTime">
                <p>{{objFormEmailSingle.startRewardTime}}</p>
            </el-form-item>
            <el-form-item label="结束发奖时间">
                <p>{{objFormEmailSingle.endRewardTime}}</p>
            </el-form-item>
            <el-form-item label="申请原因">
                <p>{{objFormEmailSingle.reason}}</p>
            </el-form-item>
            <el-form-item label="角色有效开始时间">
                <p>{{objFormEmailSingle.effectiveStartingTime}}</p>
            </el-form-item>
            <el-form-item label="角色有效结束时间">
                <p>{{objFormEmailSingle.effectiveEndingTime}}</p>
            </el-form-item>
            <el-form-item label="物品">
                <p v-for="(item,idx) in objFormEmailSingle.items" :key="idx">【名称：{{item.name}}】-数量：{{item.num}}</p>
            </el-form-item>
            <el-form-item style="textAlign:center;" v-if="objFormEmailSingle.status==0&&isRole()">
                <el-button type="primary" @click="handleClickPass('objFormAnnoEdit')">审批通过</el-button>
            </el-form-item>
        </el-form>
    </el-dialog>
</div>
</template>

<script>
export default {
    name: "email-list",
    props: {
        oriTab: {
            type: String,
            default: "create"
        }
    },
    data() {
        return {
            bDialogVisible: false,
            objFormEmailSingle: {},
            aTableDataEmail: [{
                id: "111",
                serverId: ["111", "222"],
                time: "111",
                name: "2019-09-19 09：32：12",
                reason: "原因",
                status: "0", //邮件状态 0 未审批 1 通过 2 作废 3 已发放
                approverName: "审批人",
                params: "角色名或者 角色id ",
                title: "邮件标题",
                content: "邮件内容",
                startRewardTime: "2019-09-19 09：32：12",
                endRewardTime: "2019-09-19 09：32：12",
                receiverType: "1", //收件人类型 1 所有人 2 角色id  3角色名
                items: [{
                    itemId: "234",
                    num: 234
                }],
                effectiveStartingTime: "2019-09-19 09：32：12",
                effectiveEndingTime: "2019-09-19 09：32：12"
            }],
            aSelectRow: [],
            // 节点分页数据
            objPager: {
                total: 20,
                currentPage: 1,
                pageSize: 10
            }
        };
    },
    watch: {
        oriTab: {
            handler() {
                if (this.oriTab == "list") {
                    this.fetchEmailList();
                }
            }
        }
    },
    filters: {
        statusFilter(status) {
            const statusMap = ["未审批", "通过", "作废", "已发放"];
            return statusMap[Number(status)];
        }
    },
    methods: {
        isRole() {
            let userInfo = JSON.parse(sessionStorage.getItem("userInfo"));
            let role = userInfo.powerType;
            return role == '超级管理员';
        },
        returnServer(params) {
            if (params) {
                let arr = [];
                params.forEach(item => {
                    arr.push(item.name);
                });
                let show = arr.join(",");
                return show;
            }
        },
        handleSelectRows(val) {
            this.aSelectRow = val;
        },
        handlePassMulti(type) {
            let arr = this.aSelectRow.map(item => {
                return item.id;
            });
            let params = {
                mailId: arr,
                type: type
            };

            this.$fetch.post(API.email.approval_mail, params).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.fetchEmailList();
                } else {
                    this.$message.error(res.desc);
                }
            });
        },
        handleClickDetail(params) {
            this.objFormEmailSingle = params;
            this.bDialogVisible = true;
        },
        handleClickPass() {
            let params = {
                mailId: [this.objFormEmailSingle.id],
                type: "1"
            };
            this.$fetch.post(API.email.approval_mail, params).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.bDialogVisible = false;
                    this.fetchEmailList();
                } else {
                    this.$message.error(res.desc);
                }
            });
        },

        fetchEmailList() {
            let params = {
                page: this.objPager.currentPage,
                pageSize: this.objPager.pageSize
            };
            this.$fetch.post(API.email.mail_list, params).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.aTableDataEmail = res.data.mails;
                    this.objPager.total = res.data.total;
                } else {
                    this.$message.error(res.desc);
                }
            });
        },

        handlePageChange(e) {
            this.objPager.currentPage = e;
            this.fetchEmailList();
        }
    },
    mounted() {
        this.fetchEmailList();
    }
};
</script>

<style lang="stylus" scoped>
.pagination {
    text-align: right;
    margin-top: 15px;
}
</style>
