<template>
<div>
    <el-form :model="objFormEmailSingle" :rules="rules" ref="objFormEmailSingle" label-width="150px">
        <el-form-item label="服务器" prop="serverId">
            <!-- <el-cascader
          style="width:600px;"
          v-model="objFormEmailSingle.serverId"
          :options="aServerList"
          :props="{ multiple: true }"
          clearable
          @change="handleChangeCascader"
        ></el-cascader> -->

            <el-select v-model="objFormEmailSingle.serverId" multiple placeholder="请选择服务器">
                <el-option v-for="(ele,idx) in aServerList" :label="ele.name" :value="ele.serverId" :key="idx"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="收件人">
            <el-radio-group v-model="objFormEmailSingle.receiverType" prop="receiverType">
                <el-radio label="1">所有人</el-radio>
                <el-radio label="2" :disabled="objFormEmailSingle.serverId.length!==1">角色ID</el-radio>
                <el-radio label="3" :disabled="objFormEmailSingle.serverId.length!==1">角色名称</el-radio>
            </el-radio-group>
        </el-form-item>

        <el-form-item label prop="params" v-if="objFormEmailSingle.receiverType&&objFormEmailSingle.receiverType!=='1'">
            <el-input type="textarea" v-model="objFormEmailSingle.params" placeholder="请输入收件人，请用，隔开！"></el-input>
        </el-form-item>

        <el-form-item label="标题" prop="title">
            <el-input v-model="objFormEmailSingle.title" placeholder="请输入标题"></el-input>
        </el-form-item>
        <el-form-item label="内容" prop="content">
            <el-input type="textarea" v-model="objFormEmailSingle.content" placeholder="请输入内容"></el-input>
        </el-form-item>
        <el-form-item label="开始发奖时间" prop="startRewardTime">
            <el-date-picker v-model="objFormEmailSingle.startRewardTime" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder="选择开始发奖时间"></el-date-picker>
        </el-form-item>
        <el-form-item label="结束发奖时间" prop="endRewardTime">
            <el-date-picker v-model="objFormEmailSingle.endRewardTime" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder="选择结束发奖时间"></el-date-picker>
        </el-form-item>
        <el-form-item label="申请原因" prop="reason">
            <el-input v-model="objFormEmailSingle.reason" placeholder="请输入申请原因"></el-input>
        </el-form-item>
        <el-form-item label="角色有效开始时间" prop="effectiveStartingTime">
            <el-date-picker v-model="objFormEmailSingle.effectiveStartingTime" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder="选择角色有效开始时间"></el-date-picker>
        </el-form-item>
        <el-form-item label="角色有效结束时间" prop="effectiveEndingTime">
            <el-date-picker v-model="objFormEmailSingle.effectiveEndingTime" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder="选择角色有效结束时间"></el-date-picker>
        </el-form-item>

        <el-form-item label="邮件奖励内容">
            <el-button @click.prevent="addItem">添加</el-button>
        </el-form-item>
        <el-row style="marginTop:10px;" :gutter="20" v-for="(item, index) in objFormEmailSingle.items" :key="index">
            <el-col :span="1" :offset="2">
                <span style="lineHeight:40px;">{{index+1}}</span>
            </el-col>
            <el-col :span="6">
                <el-form-item label="道具" :prop="'items.' + index + '.itemId'" label-width="60px" :rules="{
      required: true, message: '道具不能为空', trigger: 'blur'
    }">
                    <el-select v-model="item.itemId" filterable placeholder="请选择道具">
                        <el-option v-for="(ele,idx) in aItemList" :label="ele.type+'-'+ele.name" :value="ele.itemId" :key="idx"></el-option>
                    </el-select>
                </el-form-item>
            </el-col>
            <el-col :span="6">
                <el-form-item label="数量" :prop="'items.' + index + '.num'" label-width="60px" :rules="{
      required: true, message: '数量不能为空', trigger: 'blur'
    }">
                    <el-input-number v-model="item.num" controls-position="right" :min="1"></el-input-number>
                </el-form-item>
            </el-col>
            <el-col :span="4">
                <el-button @click.prevent="removeItem(item)">删除</el-button>
            </el-col>
        </el-row>
        <el-form-item>
            <el-button type="primary" @click="submitForm('objFormEmailSingle')">申请发奖</el-button>
        </el-form-item>
    </el-form>
</div>
</template>

<script>
export default {
    name: "email-single",
    data() {
        var checkServer = (rule, value, callback) => {
            if (!value) {
                return callback(new Error("至少选择一个服务器"));
            } else {
                callback();
            }
        };
        return {
            aItemList: [],
            aServerList: [],
            objFormEmailSingle: {
                serverId: "",
                receiverType: "1",
                params: "",
                title: "",
                content: "",
                startRewardTime: null,
                endRewardTime: null,
                reason: "",
                effectiveStartingTime: null,
                effectiveEndingTime: null,
                items: [{
                    itemId: "",
                    num: 1
                }]
            },
            rules: {
                serverId: [{
                    validator: checkServer,
                    required: true,
                    trigger: "change"
                }],
                receiverType: [{
                    required: true,
                    message: "请选择收件人",
                    trigger: "change"
                }],
                params: [{
                    required: true,
                    message: "请输入id",
                    trigger: "blur"
                }],
                title: [{
                    required: true,
                    message: "请输入标题",
                    trigger: "blur"
                }],
                content: [{
                    required: true,
                    message: "请输入公告内容",
                    trigger: "blur"
                }],
                startRewardTime: [{
                    required: true,
                    message: "请选择时间",
                    trigger: "change"
                }],
                endRewardTime: [{
                    required: true,
                    message: "请选择时间",
                    trigger: "change"
                }],
                reason: [{
                    required: true,
                    message: "请输入申请原因",
                    trigger: "blur"
                }],
                effectiveStartingTime: [{
                    required: true,
                    message: "请选择时间",
                    trigger: "change"
                }],
                effectiveEndingTime: [{
                    required: true,
                    message: "请选择时间",
                    trigger: "change"
                }]
            }
        };
    },
    methods: {
        removeItem(item) {
            var index = this.objFormEmailSingle.items.indexOf(item);
            if (index !== -1) {
                this.objFormEmailSingle.items.splice(index, 1);
            }
        },
        addItem() {
            this.objFormEmailSingle.items.push({
                itemId: "",
                num: 1
            });
        },
        handleChangeCascader(params) {},
        //   提交
        submitForm(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    let objCopy = JSON.parse(JSON.stringify(this.objFormEmailSingle));
                    this.$fetch.post(API.email.add_mail, objCopy).then(response => {
                        let res = response.data;
                        if (res.status == 200) {
                            this.$message.success(res.desc)
                            this.$emit('changeTabS', 'list')
                            this.$refs[formName].resetFields();
                        } else {
                            this.$message.error(res.desc);
                        }
                    });
                } else {
                    return false;
                }
            });
        },
        fetchItemList() {
            this.$fetch.post(API.email.item_list).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.aItemList = res.data;
                } else {
                    this.$message.error(res.desc);
                }
            });
        },
        fetchServerList() {
            this.$fetch.post(API.email.server_list).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.aServerList = res.data;
                } else {
                    this.$message.error(res.desc);
                }
            });
        }
    },
    mounted() {
        this.fetchServerList();
        this.fetchItemList();
    }
};
</script>
