<template>
<div>
    <el-form :model="objFormEmailMulti" :rules="rules" ref="objFormEmailMulti" label-width="150px">
        <el-form-item label="订单流水号" prop="sequenceId">
            <el-input v-model="objFormEmailMulti.sequenceId" placeholder="请输入订单流水号"></el-input>
        </el-form-item>
        <el-form-item label="批量发放文件">
            <el-upload :action="uploadUrl" :on-success="uploadSuccess" :on-error="uploadError" :multiple="false" accept="csv" :limit="1" :on-exceed="uploadExceed">
                <el-button size="small" type="primary">点击上传</el-button>
                <div slot="tip" class="el-upload__tip">只能上传excel文件，且不超过500kb</div>
            </el-upload>
        </el-form-item>
        <el-form-item label="标题" prop="title">
            <el-input v-model="objFormEmailMulti.title" placeholder="请输入标题"></el-input>
        </el-form-item>
        <el-form-item label="内容" prop="content">
            <el-input type="textarea" v-model="objFormEmailMulti.content" placeholder="请输入内容"></el-input>
        </el-form-item>
        <el-form-item label="开始发奖时间" prop="startRewardTime">
            <el-date-picker v-model="objFormEmailMulti.startRewardTime" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder="选择开始发奖时间"></el-date-picker>
        </el-form-item>
        <el-form-item label="结束发奖时间" prop="endRewardTime">
            <el-date-picker v-model="objFormEmailMulti.endRewardTime" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder="选择结束发奖时间"></el-date-picker>
        </el-form-item>
        <el-form-item label="申请原因" prop="reason">
            <el-input v-model="objFormEmailMulti.reason" placeholder="请输入申请原因"></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="submitFormMulti('objFormEmailMulti')">申请发奖</el-button>
        </el-form-item>
    </el-form>
</div>
</template>

<script>
import baseURL from "../../config/baseUrl";
export default {
    name: "email-multi",
    data() {
        return {
            objFormEmailMulti: {
                sequenceId: "",
                fileId: null,
                title: "",
                content: "",
                startRewardTime: null,
                endRewardTime: null,
                reason: ""
            },
            rules: {
                sequenceId: [{
                    required: true,
                    message: "请输入标题",
                    trigger: "blur"
                }],
                title: [{
                    required: true,
                    message: "请输入标题",
                    trigger: "blur"
                }],
                content: [{
                    required: true,
                    message: "请输入公告内容",
                    trigger: "blur"
                }],
                startRewardTime: [{
                    required: true,
                    message: "请选择时间",
                    trigger: "change"
                }],
                endRewardTime: [{
                    required: true,
                    message: "请选择时间",
                    trigger: "change"
                }],
                reason: [{
                    required: true,
                    message: "请输入申请原因",
                    trigger: "blur"
                }]
            }
        };
    },
    computed: {
        uploadUrl() {
            return baseURL + API.upload;
        }
    },
    methods: {
        submitFormMulti(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    let objCopy = JSON.parse(JSON.stringify(this.objFormEmailMulti));
                    this.$fetch.post(API.email.add_mail_batch, objCopy).then(response => {
                        let res = response.data;
                        if (res.status == 200) {
                            this.$emit('changeTabM', 'list')
                            this.$refs[formName].resetFields();
                            this.$message.success(res.desc)
                        } else {
                            this.$message.error(res.desc);
                        }
                    });
                } else {
                    return false;
                }
            });
        },

        fetchServerList() {
            this.$fetch.post(API.anno.server_list).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.aServerList = res.data;
                } else {
                    this.$message.error(res.desc);
                }
            });
        },

        uploadSuccess(response, file, fileList) {
            if (response.status == 200) {
                this.objFormEmailMulti.fileId = response.data.fileId;
                this.$message.success(response.desc);
            } else {
                this.$message.error(response.desc);
            }
        },
        uploadError(err, file, fileList) {},
        uploadExceed(files, fileList) {
            this.$message.error("文件上传数量超出上限！");
        }
    }
};
</script>
